.header {
  background-color: #eee;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  padding: 10px;
  font-size: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: center;
      align-content: center;
  /* background-color: aqua; */
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 100%;
}

h3 {
  font-family: "Pacifico", cursive;
  margin: 0;
}

.search_bar {
  width: 40%;
  border: none;
  font-size: 16px;
  padding: 10px;
}

.icon_styling {
  color: rgb(51, 46, 46);
}

.footer {
  bottom: 0;
  left: 0;
  background-color: #eee;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  position: fixed;
  background-color: "#eee";
  padding: 10px;
  font-size: 20px;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  z-index: 100;
}

.header_nav {
  display: -ms-flexbox;
  display: flex;
  width: 18%;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-align: center;
      align-items: center;
}

.userPic {
  height: 25px;
  width: 25px;
  border-radius: 50%;
  border: 1px solid #ccc;
}

@media only screen and (min-width: 550px) {
  .footer {
    display: none;
  }
}

@media (max-width: 550px) {
  .header {
    -ms-flex-pack: center;
        justify-content: center;
  }

  .header_nav {
    display: none;
  }

  .search_bar {
    display: none;
    visibility: hidden;
  }
}



