.userSearch_container {
  -webkit-box-shadow: 4px 4px 2px #eee;
          box-shadow: 4px 4px 2px #eee;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding: 15px;
  margin: 5%;
}

.user_container {
  width: 20%;
  text-align: center;
}

.pic_container {
  width: 100%;
  text-align: center;
  display: block;
}

.user_img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

@media (max-width: 1080px) {
  .userSearch_container {
    margin: 10%;
  }
}

.username {
  text-decoration: none;
  font-family: "pacifico", cursive;
  color: black;
  font-size: 20px;
}

